import * as React from "react";
import * as Styles from "components/ShapesPalette.css";
import {connect} from "react-redux";
import {IAppState, IAction, IPaletteAction, ShapeType} from "app";
import {Circle, Square, Triangle} from "components/Shapes";

interface IProps {
  selectedShape?: ShapeType;
}

interface IDispatch {
  dispatch(action: IAction): void;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShape: state.selectedShape,
  };
}

class ShapesPaletteBase extends React.Component<IProps & IDispatch> {
  public render(): JSX.Element {
    const {selectedShape} = this.props;

    const shapes = [
      <Circle cx={50} cy={100} onClick={this.clickHandler} isSelected={selectedShape === ShapeType.Circle} />,
      <Square cx={50} cy={200} onClick={this.clickHandler} isSelected={selectedShape === ShapeType.Square} />,
      <Triangle cx={50} cy={300} onClick={this.clickHandler} isSelected={selectedShape === ShapeType.Triangle} />,
    ];

    return <svg className={Styles.palette}>{shapes}</svg>;
  }

  private clickHandler = (shapeType: ShapeType) => {
    this.props.dispatch({type: "SELECT_SHAPE", shapeType} as IPaletteAction);
  };
}

export const ShapesPalette = connect(mapStateToProps)(ShapesPaletteBase);
