import * as React from "react";
import * as Styles from "components/Shapes.css";
import {ShapeType} from "app";

interface IProps {
  cx: number;
  cy: number;
  isSelected?: boolean;
  onClick(id: ShapeType): void;
}

interface ICommonSvgProps {
  stroke: string;
  strokeWidth: number;
  fill: string;
  className?: string;
  onClick(): void;
}

abstract class Shape extends React.Component<IProps> {
  abstract get shapeType(): ShapeType;
  protected readonly size = 60;
  protected buildCommonSvgProps(): ICommonSvgProps {
    return {
      stroke: "black",
      strokeWidth: 1,
      fill: "#dfe3fb",
      className: this.props.isSelected ? Styles.selected : undefined,
      onClick: () => {
        this.props.onClick(this.shapeType);
      },
    };
  }
}

export class Circle extends Shape {
  public get shapeType(): ShapeType {
    return ShapeType.Circle;
  }

  public render(): JSX.Element {
    return <circle cx={this.props.cx} cy={this.props.cy} r={this.size / 2} {...this.buildCommonSvgProps()} />;
  }
}

export class Square extends Shape {
  public get shapeType(): ShapeType {
    return ShapeType.Square;
  }

  public render(): JSX.Element {
    return (
      <rect
        x={this.props.cx - this.size / 2}
        y={this.props.cy - this.size / 2}
        width={this.size}
        height={this.size}
        {...this.buildCommonSvgProps()}
      />
    );
  }
}

export class Triangle extends Shape {
  public get shapeType(): ShapeType {
    return ShapeType.Triangle;
  }

  public render(): JSX.Element {
    const r = this.size * 0.43;
    const points: string[] = [
      [this.props.cx, this.props.cy - r].join(","),
      [this.props.cx - this.size / 2, this.props.cy + r].join(","),
      [this.props.cx + this.size / 2, this.props.cy + r].join(","),
    ];
    return <polygon points={points.join(" ")} {...this.buildCommonSvgProps()} />;
  }
}
